<template>
    <div class='addvipkind'>
        <div class='top'>
          <span class='fundInfo' @click='goback()'>会员卡种类</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <div class='vipkindinfo'>
          <div class="kindleft">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
              <el-form-item label="会员卡类型: " prop="vipCardType" class='formNew flexContent' style="border: 0; background: none; padding:0">
                <el-radio-group v-model="ruleForm.vipCardType" size="small" v-if="modify">
                  <el-radio-button label="PREPAID" v-show="ruleForm.vipCardType == 'PREPAID'">
                    <img src="../../assets/svg/icon_select.svg"/>
                    储值卡</el-radio-button>
                  <el-radio-button label="TIMES" v-show="ruleForm.vipCardType == 'TIMES'">
                    <img src="../../assets/svg/icon_select.svg"/>
                    次卡</el-radio-button>
                  <el-radio-button label="PACKAGE" v-show="ruleForm.vipCardType == 'PACKAGE'">
                    <img src="../../assets/svg/icon_select.svg"/>
                    期限卡</el-radio-button>
                    <el-radio-button label="DATELIMIT" v-show="ruleForm.vipCardType == 'DATELIMIT'">
                    <img src="../../assets/svg/icon_select.svg"/>
                    会籍卡</el-radio-button>
                </el-radio-group>
                <el-radio-group v-model="ruleForm.vipCardType" size="small" v-if="!modify">
                  <el-radio-button label="PREPAID">
                    <img src="../../assets/svg/icon_select.svg" v-show="ruleForm.vipCardType == 'PREPAID'"/>
                    储值卡</el-radio-button>
                  <el-radio-button label="TIMES">
                    <img src="../../assets/svg/icon_select.svg" v-show="ruleForm.vipCardType == 'TIMES'"/>
                    次卡</el-radio-button>
                  <el-radio-button label="PACKAGE">
                    <img src="../../assets/svg/icon_select.svg" v-show="ruleForm.vipCardType == 'PACKAGE'"/>
                    期限卡</el-radio-button>
                  <el-radio-button label="DATELIMIT">
                    <img src="../../assets/svg/icon_select.svg" v-show="ruleForm.vipCardType == 'DATELIMIT'"/>
                    会籍卡</el-radio-button>  
                </el-radio-group>
              </el-form-item>
              <el-form-item label="会员卡种类名称：" prop='name' class='formNew'>
                  <el-input v-model="ruleForm.name" placeholder="请输入会员卡种类" maxlength = '15'></el-input>
              </el-form-item>
              <el-form-item label="会员卡介绍：" class='formNew formArea'>
                  <el-input
                    type="textarea"
                    :rows="4"
                    placeholder="请输入内容"
                    v-model="ruleForm.brief"
                    maxlength="2999"
                    >
                  </el-input>
              </el-form-item>
            </el-form>
          </div>
          <div class="kindright">
            <el-form :model="ruleForm" :rules="rules" class="demo-ruleForm" label-width="100px">
              <el-form-item label="适用门店：" class='applyBranch'>
                  <el-checkbox :indeterminate="isIndeterminateBranch" v-model="checkBranchAll" @change="handleCheckAllChange">全选</el-checkbox>
                    <div style="margin: 15px 0;"></div>
                    <div class="errorTip" v-if="errorTip">{{errorTip}}</div>
                  <el-checkbox-group v-model="ruleForm.applyBranchIds" @change="handleCheckedChange">
                    <el-checkbox v-for="(item, index) in branchList" :label="item.id"  :key="index">{{item.name}}</el-checkbox>
                  </el-checkbox-group>
              </el-form-item>
              <el-form-item label="限制条件：" class='limitSelectList'>
                  <el-checkbox-group v-model="ruleForm.limitSelect" @change="handleLimitChange">
                    <el-checkbox v-for="(item, index) in limitSelectList" :label="item" :key="index">{{item}}</el-checkbox>
                  </el-checkbox-group>
              </el-form-item>
              <el-form-item label=" " class='limitCourseNum' v-if="ruleForm.vipCardType == 'PACKAGE'">
                  <el-checkbox v-model="checkedCourseNum" @change="handleCheckedCourseChange">限制单位时间可预约课程数</el-checkbox>
                  <div class="courseNumSelect" v-if="checkedCourseNum">
                    <el-form-item label="周期：" class='formCourseNum'>
                      <el-select v-model="ruleForm.limitCondition.timeUnit" placeholder="请选择">
                        <el-option
                          v-for="item in CourseNumList"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="节数："  class='formCourseNum bg'>
                      <el-input v-model="ruleForm.limitCondition.reserveNumberLimit" placeholder="" maxlength = '5' @blur="checkNumber(ruleForm.limitCondition.reserveNumberLimit)"></el-input>
                      <div class="error" v-if="errortext">{{errortext}}</div>
                  </el-form-item>
                  </div>
              </el-form-item>
              <el-form-item label=" " class='courseSelectList' v-if="ruleForm.vipCardType == 'DATELIMIT' || ruleForm.vipCardType == 'TIMES'"> 
                  <el-checkbox v-model="checkedTime" @change="handleCheckedCourseChange">使用时间</el-checkbox>
                  <div class="timeSelect" v-if="checkedTime">
                    <el-time-select
                      @change="changeStartTime()"
                      placeholder="开始"
                      v-model="startTime"
                      :picker-options="{
                        start: '06:00',
                        step: '00:30',
                        end: '21:00'
                      }">
                    </el-time-select>
                    <span>到</span>
                    <el-time-select
                      @change="changeEndTime()"
                      placeholder="结束"
                      v-model="endTime"
                      :picker-options="{
                        start: '06:30',
                        step: '00:30',
                        end: '22:00',
                        minTime: startTime
                      }">
                    </el-time-select>
                  </div>
              </el-form-item>
            </el-form>
          </div>
          <div class='btn2'>
            <div class='cancle' @click="cancle">取消</div>
            <div class='submit' @click="submit('ruleForm')">确定</div>
          </div>
        </div>
        <div class='content'>
            <div class='context'>操作记录</div>
        </div>
        <Table  :tableData="tableData" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" :currentPage="currentPage" :isShowSelectionBtn="isShowSelectionBtn" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {getBranchName, getallprivate, getCardChangeList, addCardType, updateCardType, getCardRecords} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      ruleForm: {
        vipCardType: '',
        name: '',
        brief: '',
        applyBranchIds: [],      // 分店
        limitSelect: [],       // 限制条件
        courseLimits: [],    // 课程选择
        limitCondition: {buyLimit: '', useLimit: '', useTimeStart: null, useTimeEnd: null, timeUnit: null, reserveNumberLimit: null},
        id: ''
      },
      startTime: '',
      endTime: '',
      rules: {
        name: [
          { required: true, message: '请输入种类名称', trigger: 'blur' }
        ],
        vipCardType: [
          { required: true, message: '请选择会员卡种类', trigger: 'blur' }
        ]
      },
      checkedTime: false,  // 使用时间开关
      checkedCourseNum: false, // 限制课程开关
      courseLimits: [],
      checkBranchAll: false,     // 分店全选状态
      checkedKind: false,        // 私教课选择开关
      branchList: [],
      isIndeterminateBranch: true,
      // limitSelectList: [{buyLimit: 'INSALE', value: '仅限本人使用'}, {useLimit: 'ONLYSELF', value: '不对外售卖'}],
      limitSelectList: ['仅限本人使用', '不对外售卖'],
      courseSelectList: [],
      tableData: [
      ],
      tableKey: [
        {name: '序号', value: 'seqNo', seqNo: true},
        {name: '操作时间', value: 'createTime', nor: true},
        {name: '操作人', value: 'operator', nor: true},
        {name: '操作内容', value: 'operation', nor: true}
      ],
      selectList: [],
      setInfo: 60,
      isShowSelectionBtn: false,
      isPage: true,
      total: 0,
      currentPage: 1,
      pageSize: 5,
      tabhide: true,
      gymList: {},
      text: '添加会员卡种类',
      cardTypeCode: '',
      modify: '',
      errorTip: '',
      errortext: '',
      CourseNumList: [
        {value: 'DAY', label: '一天'},
        {value: 'WEEK', label: '一周'},
        {value: 'MONTH', label: '一月'},
      ]
    }
  },
  components: {
    Table
  },
  created () {
    // 获取分店信息
    this.initBranch()
    // 获取私教课种类信息
    this.initCourseKind()
    this.modify = this.$route.query.modify
    if (this.modify) {
      let detailTata = getSessionStorage('detailInfo', {}) // 从之前的页面跳转传过来的数据
      this.ruleForm.name = detailTata.name
      this.ruleForm.brief = detailTata.brief
      this.ruleForm.vipCardType = detailTata.vipCardType
      for (var i=0; i<detailTata.applyGyms.length; i++) {
        this.ruleForm.applyBranchIds.push(detailTata.applyGyms[i].id)
      }
      if (detailTata.courseLimits && detailTata.courseLimits.length > 0) {
        this.checkedKind = true
        for (var i=0; i<detailTata.courseLimits.length; i++) {
          this.ruleForm.courseLimits.push(parseInt(detailTata.courseLimits[i].id))
        }
      }
      this.ruleForm.id = detailTata.id
      this.cardTypeCode = detailTata.code
      if (detailTata.buyLimit == 'INSALE') {
        this.ruleForm.limitCondition.buyLimit = 'INSALE'
        this.ruleForm.limitSelect.push('不对外售卖')
      }
      if (detailTata.useLimit == 'ONLYSELF') {
        this.ruleForm.limitCondition.useLimit = 'ONLYSELF'
        this.ruleForm.limitSelect.push('仅限本人使用')
      }
      this.text = '修改会员卡种类'
      if (detailTata.useTimeStart && detailTata.useTimeEnd) {
        this.checkedTime = true
        this.startTime = this.filterNum(detailTata.useTimeStart)
        this.endTime = this.filterNum(detailTata.useTimeEnd)
      }
      this.ruleForm.limitCondition.timeUnit = detailTata.timeUnit
      this.ruleForm.limitCondition.reserveNumberLimit = detailTata.reserveNumberLimit
      if (detailTata.timeUnit && detailTata.reserveNumberLimit) {
        this.checkedCourseNum = true
        this.ruleForm.limitCondition.timeUnit = detailTata.timeUnit
        this.ruleForm.limitCondition.reserveNumberLimit = detailTata.reserveNumberLimit
      }
      // this.ruleForm.limitCondition.timeUnit = 'DAY'
      // this.ruleForm.limitCondition.reserveNumberLimit = 22
      // this.checkedTime = true
      // this.ruleForm.useTimeStart = '2:30'
      // this.ruleForm.useTimeEnd = '12:30'
    } else {
      this.handleCheckAllChange()
      console.log('回去数', this.ruleForm)
    }
    // 获取会员卡种类列表信息
    this.initData()
  },
  methods: {
    goback () {
      this.$router.push('vipcard')
    },
    handleCheckAllChange(val) {
      this.ruleForm.applyBranchIds.length = []
      if (val) {
        for (let i = 0; i < this.branchList.length; i++) {
          this.ruleForm.applyBranchIds.push(this.branchList[i].id)
        }
      } else {
        this.ruleForm.applyBranchIds = []
      }
      // this.ruleForm.applyBranchIds.length = 0
      this.isIndeterminateBranch = false
      
    },
    handleLimitChange (value) {
      // console.log(value)
      this.ruleForm.limitCondition.buyLimit = ''
      this.ruleForm.limitCondition.useLimit = ''
      for (var i = 0; i < value.length; i++) {
        if (value[i] == '仅限本人使用') {
          this.ruleForm.limitCondition.useLimit = 'ONLYSELF'
        }
        if (value[i] == '不对外售卖') {
          this.ruleForm.limitCondition.buyLimit = 'INSALE'
        }
      }
      // console.log('this.ruleForm.limitCondition')
      // console.log(this.ruleForm.limitCondition)
    },
    handleCheckedChange (value) {
      // console.log(value)
      this.ruleForm.applyBranchIds = value
      let checkedCount = value.length
      this.checkBranchAll = checkedCount === this.branchList.length
      this.isIndeterminateBranch = checkedCount > 0 && checkedCount < this.branchList.length
    },
    handleCheckedCourse (value) {
      // console.log(value)
      this.courseLimits = value
    },
    handleCheckedCourseChange () {
      if (!this.checkedKind) {
        this.$set(this.ruleForm, 'courseLimits', [])
      }
      if (!this.checkedTime) {
        this.ruleForm.limitCondition.useTimeEnd = null
        this.ruleForm.limitCondition.useTimeEnd = null
      }
      if (!this.checkedCourseNum) {
        this.ruleForm.limitCondition.timeUnit = null
        this.ruleForm.limitCondition.reserveNumberLimit = null
      }
    },
    // 获取查询参数
    getQueryParam (queryObj) {
    },
    changeParam (data) {
      this.currentPage = data
      this.initData()
      // console.log('获取数据', data)
    },
    async getRowdata (data) {
    },
    // 获取分店信息
    async initBranch () {
      const res = await getBranchName()
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.branchList = res.data
        }
        // console.log('获取分店信息', this.branchList)
      }
    },
    // 获取私教课种类信息
    async initCourseKind () {
      const res = await getallprivate()
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.courseSelectList = res.data
        }
        // console.log('获取私教课种类信息', this.courseSelectList)
      }
    },
    cancle () {
      this.$router.push('vipcard')
    },
    checkNumber (num) {
      // this.form.tailPeriod = parseInt(event)
      if (num) {
        // num = parseInt(num)
        var reg = /(^[0-9][0-9]*(.[0-9]+)?)$/
        var patten =  new RegExp(reg)
        // console.log(num.toString() == 'NaN')
        if (!patten.test(num)) {
          this.errortext = '课程数为正整数'
        } else {
          this.errortext = ''
        }
      } else {
        this.errortext = ''
      }
    },
    checkbranch () {
      if (this.ruleForm.applyBranchIds.length == 0) {
        this.errorTip = '适用分店不能为空'
        return
      } else {
        this.errorTip = ''
      }
    },
    // 添加、修改会员卡种类
    async submit (ruleForm) {
      let self = this
      this.checkbranch()
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (!this.modify) {
            if (this.errorTip) {
              return
            }
            let obj = {
              applyBranchIds: this.ruleForm.applyBranchIds,
              brief: this.ruleForm.brief,
              limitCondition: this.ruleForm.limitCondition,
              courseLimits: this.courseLimits,
              vipCardType : this.ruleForm.vipCardType,
              name: this.ruleForm.name
            }
            addCardType(obj).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '增加成功',
                  type: 'success'
                })
                self.$router.push({name: 'vipcard'})
              } else {
                self.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }
            })
          } else {
            let obj = {
              applyBranchIds: this.ruleForm.applyBranchIds,
              brief: this.ruleForm.brief,
              limitCondition: this.ruleForm.limitCondition,
              courseLimits: this.ruleForm.courseLimits,
              id : this.ruleForm.id,
              name: this.ruleForm.name,
              vipCardType: this.ruleForm.vipCardType
            }
            updateCardType(obj).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
                })
                self.$router.push({name: 'vipcard'})
              } else {
                self.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          }
        }
      })
    },
    // 获取健身房所以会员卡种类列表
    async initData () {
      if (!this.cardTypeCode) {
        return
      }
      const res = await getCardChangeList(this.currentPage, this.pageSize, this.cardTypeCode)
      if (isApiResponseOk(res) && res.data) {
        this.total = res.data.totalNum
        this.tableData = res.data.record
      } else {
        this.isPage = false
      }
    },
    changeStartTime () {
      this.ruleForm.limitCondition.useTimeStart = this.filterTime(this.startTime)
    },
    changeEndTime () {
      this.ruleForm.limitCondition.useTimeEnd = this.filterTime(this.endTime)
    },
    filterTime (str) {
      if (!str) {
        return
      }
      str = str.toString()
      return parseInt(str.replace(':', ''))
    },
    filterNum (num) {
      num = num.toString()
      if (num.length == 3) {
        num = '0'+num
      }
      if (num.length == 2) {
        num = '00'+num
      }
      num = num.substring(0,2) + ':' + num.substring(2,4)
      return num
    }
  }
}
</script>
<style lang="sass" scoped>
.addvipkind
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
