            cursor: pointer;
        .branch
            font-size: .24rem;
            font-family: PingFangSC-Semibold;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;  
    .content
        display: flex;
        align-items: center;
        height: .36rem;
        margin-top: .6rem;
        .context
            color: #8597A1;
            font-size: .22rem;
            flex: 2;
    .vipkindinfo
      width: 100%;
      height: 5.3rem;
      background: #fff;
      box-sizing: border-box;
      padding-top: 0.5rem;
      padding-left: 0.4rem;
      position: relative;
      display: flex;
</style>
