}
.vipkindinfo .kindright .applyBranch .el-checkbox-group{
  max-height: 1.2rem;
  overflow: auto;
}
.kindright .el-checkbox{
  /* width: 1.2rem; */
  margin-left: 0!important; 
  padding-right: 0.2rem;
  line-height: 0.4rem;
  height: 0.4rem;
}
.kindright .applyBranch .el-checkbox{
  padding-right: 0.3rem;
  /* width: 1.2rem; */
}
.kindright .limitSelectList .el-checkbox{
  margin-right: 0.2rem; 
}
.courseSelectList{
  position: relative;
}
.courseSelectList .el-form-item__content{
  display: flex;
  flex-direction: column;
}
.limitCourseNum.el-form-item{
  display: flex;
  margin-bottom: 0;
}
.limitSelectList.el-form-item{
  margin-bottom: 0!important;
}
.courseNumSelect .el-form-item.formCourseNum.bg{
  background: #FBFBFC;
}
.el-form-item.formCourseNum.bg .error{
  line-height: 0.3rem;
  color: #f56c6c;
}
.courseNumSelect .el-form-item.formCourseNum{
  width: 2.2rem;
  height: 0.4rem;
  border: 1px solid #EAECEF;
  background: #fff;
  font-size: 0.14rem;
  color: #8597A1;
  display: flex;
  padding-left: 0.1rem;
  box-sizing: border-box;
  margin-bottom: 6px!important;
}
.courseNumSelect .el-form-item.formCourseNum .el-form-item__label{
  width: auto;
}
.courseSelectList .el-form-item__content .timeSelect{
  display: flex;
  justify-content: space-between;
  width: 3rem;
  align-items: center;
}

.courseSelectList .el-form-item__content .timeSelect .el-date-editor--time-select{
  width: 1.2rem;
  height: 0.4rem;
  background: #FFFFFF;
  border: 1px solid #E9ECEF;
}
.timeSelect .el-date-editor--time-select .el-input__icon{
  /* display: none */
}


.courseSelectList .courseSelect img{
  width: 16px;
  height: 16px;
  margin-top: 0.12rem;
}
.courseSelectList .el-form-item__content{
  display: flex;
}
.courseSelectList .courseSelect{
  display: flex;
}
.kindright .courseSelectList .courseSelect .el-checkbox-group{
  margin-left: 20px!important;
  display: flex;
  /* flex-direction: column; */
  overflow: auto;
  max-height: 1.6rem;
  width: 100%;
} 
.kindright .courseSelectList .courseSelect .el-checkbox-group .el-checkbox{
  width: 100%;
}
.addvipkind .btn2{
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 0.2rem;
  left: 50%;
  transform: translateX(-50%);
  width: 2.6rem;
}
.addvipkind .btn2 .submit, .addvipkind .cancle{
  background: #20C6BA;
  border-radius: .04rem;
  color: #fff;
  font-size: .14rem;
  width: 1.2rem;
  height: .44rem;
  line-height: .44rem;
  color: #fff;
  text-align: center;
  margin-top: .8rem;
  cursor: pointer;
}
.addvipkind .cancle{
 background: #C0C4CC;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background: #20C6BA!important;
    border-color: #20C6BA!important;
}
.el-checkbox__input.is-checked+.el-checkbox__label {
    color: #606266!important;
}
.addvipkind .table .el-table__row td{
  border-bottom: 1px solid #EAECEF;
  background: #f4f7f9;
}
.vipkindinfo ::-webkit-scrollbar{
  width: 4px;  
  height: 16px;  
  background-color: #dcdfe6;
}
.vipkindinfo ::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.vipkindinfo ::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}

.time-select .el-picker-panel__content::-webkit-scrollbar{
  width: 4px;  
  height: 0;  
  background-color: #dcdfe6;
}
.time-select .el-picker-panel__content::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.time-select .el-picker-panel__content::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}
</style>



